import './App.css';
import { HashRouter, Routes, Route } from "react-router-dom";
import HomeView from './views/home/homeView';
import AppBar from './views/home/appBar';
import CatalogueView from './views/catalogue/catalogueView';
import ContactView from './views/contactez-nous/contactView';
import DetailsView from './views/catalogue/detailsView';

import pots from './data/pots/potsData';
import {
  PotStandard,
  GrandConteneurPe,
  ModeleBas,
  PotBriqueStandard,
  PotCarreStandard,
  PotRondCarre,
  PotRosier,
  PotVolute,
  SoucoupeStandard,
  CoupeVolute,
  CoupeMillesia,
  CoupeOcea,
  CoupePrimaPlus,
  JardiniereCouleur,
  JardiniereVolute,
  JardinierePrimaPlus,
  JardiniereClara,
  BacAMousse
} from './data/pots/img'

function App() {
  /* Catalogue Pots */
  const potsTitle = "Pots et soucoupes";
  const potsPages = [
    {
      title: "Pots Standard",
      img: PotStandard
    },
    {
      title: "Grand Conteneur PE",
      img: GrandConteneurPe
    },
    {
      title: "Modles Bas",
      img: ModeleBas
    },
    {
      title: "Pots Carrs Standard",
      img: PotCarreStandard
    },
    {
      title: "Pots Ronds Carrs",
      img: PotRondCarre
    },
    {
      title: "Pots Rosiers",
      img: PotRosier
    },
    {
      title: "Pots Brique Standard",
      img: PotBriqueStandard
    },
    {
      title: "Pots Volutes",
      img: PotVolute
    },
  ];

  const coupesPages = [
    {
      title: "Coupes Volutes",
      img: CoupeVolute
    },
    {
      title: "Coupes Prima +",
      img: CoupePrimaPlus
    },
    {
      title: "Coupes Millesia",
      img: CoupeMillesia
    },
    {
      title: "Coupes Ocea",
      img: CoupeOcea
    },
  ];

  const soucoupesPages = [
    {
      title: "Soucoupes Standard",
      img: SoucoupeStandard
    },
  ];

  const jardinieresPages = [
    {
      title: "Jardinires Couleurs",
      img: JardiniereCouleur
    },
    {
      title: "Jardinires Volutes",
      img: JardiniereVolute
    },
    {
      title: "Jardinires Prima +",
      img: JardinierePrimaPlus
    },
    {
      title: "Jardinires Clara",
      img: JardiniereClara
    },
  ];

  const bacsAMoussePages = [
    {
      title: "Bac A Mousse",
      img: BacAMousse
    },
  ];

  const potsEtSoucoupesLink = "/catalogue/pots/"
  const potsEtSoucoupesSectionsTitle = ["Pots", "Coupes", "Soucoupes", "Jardinires", "Bacs  Mousse"];
  const potsEtSoucoupesPages = [...potsPages, ...coupesPages, ...soucoupesPages, ...jardinieresPages, ...bacsAMoussePages]

  /* Catalogue Florales */
  const floralesTitle = "Florales";
  const floralesPages = [
    {
      title: "Jardinires Prima +",
      link: "jardinieres-prima-plus",
      img: JardinierePrimaPlus
    }
  ];

  /* Catalogue Emballages */
  const emballagesTitle = "Emballages";
  const emballagesPages = [
    {
      title: "Jardinires Clara",
      link: "jardinieres-clara",
      img: JardiniereClara
    }
  ];

  /* Catalogue Arts Maisons */
  const artsMaisonsTitle = "Arts Maisons";
  const artsMaisonPages = [
    {
      title: "Jardinires Volutes",
      link: "jardinieres-volutes",
      img: JardiniereVolute
    }
  ];

  return (
    <>
      <AppBar></AppBar>
      <div className="body">
        <HashRouter>
          <Routes>
            <Route exact path="/" element={ <HomeView /> } />
            <Route path="/catalogue/pots" element={ <CatalogueView title={potsTitle} pageLink={potsEtSoucoupesLink} sections={[potsPages, coupesPages, soucoupesPages, jardinieresPages, bacsAMoussePages]} sectionsTitle={potsEtSoucoupesSectionsTitle} /> } />
            {potsEtSoucoupesPages.map((page, index) => (
              <Route path={"/catalogue/pots/" + String(page.title).toLowerCase().replace(/ /g, '-').replace('+',"plus").normalize("NFD").replace(/\p{Diacritic}/gu, "")} element={ <DetailsView index={index} pages={potsEtSoucoupesPages} data={pots} /> } />
            ))}
            <Route path="/catalogue/florales" element={ <CatalogueView title={floralesTitle} pages={floralesPages} /> } />
            <Route path="/catalogue/emballages" element={ <CatalogueView title={emballagesTitle} pages={emballagesPages} /> } />
            <Route path="/catalogue/arts%20maisons" element={ <CatalogueView title={artsMaisonsTitle} pages={artsMaisonPages} /> } />
          </Routes>
        </HashRouter>
      </div>
      <footer>
          <span>En cours de construction</span>
      </footer>
    </>
  );
}

export default App;
