import * as React from 'react';
import { Link } from "react-router-dom";
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import ImageListItemBar from '@mui/material/ImageListItemBar';

export default function CatalogueView(props) {
  const { title, pageLink, sections, sectionsTitle } = props;
  
  return (
    <>
      <h1 className="centered">{title}</h1>
      {
        sections.map((section, index) => (
          <>
            <h2>{sectionsTitle[index]}</h2>

            <div className="centered">
              <ImageList sx={{ '&.MuiImageList-root': { 'gridTemplateColumns': 'repeat(3, 1fr) !important', gap: '35px !important' }, display: { xs: 'none', md: 'grid' }}}>
                {
                  section.map(s => (
                    <ImageListItem key={s.img}>
                      <Link to={pageLink + String(s.title).toLowerCase().replace(/ /g, '-').replace('+',"plus").normalize("NFD").replace(/\p{Diacritic}/gu, "")}>
                        <img
                          className='vignette'
                          src={`${s.img}?w=248&fit=crop&auto=format`}
                          srcSet={`${s.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
                          alt={s.title}
                          loading="lazy"
                        />
                        <ImageListItemBar
                          title={s.title}
                          position="below"
                        />
                      </Link>
                    </ImageListItem>
                  ))
                }
              </ImageList>

              <ImageList sx={{ '&.MuiImageList-root': { 'gridTemplateColumns': 'repeat(1, 1fr) !important', gap: '0px !important' }, display: { xs: 'grid', md: 'none' }}}>
                {
                  section.map(s => (
                    <ImageListItem key={s.img}>
                      <Link to={pageLink + String(s.title).toLowerCase().replace(/ /g, '-').replace('+',"plus").normalize("NFD").replace(/\p{Diacritic}/gu, "")}>
                        <img
                          src={`${s.img}?w=248&fit=crop&auto=format`}
                          srcSet={`${s.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
                          alt={s.title}
                          loading="lazy"
                        />
                        <ImageListItemBar
                          title={s.title}
                          position="below"
                        />
                      </Link>
                    </ImageListItem>
                  ))
                }
              </ImageList>
            </div>
          </>
        ))
      }
    </>
  );
}
