import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Divider from '@mui/material/Divider';

const pages = [];
const cataloguePages = ['Pots', 'Emballages', 'Florales', 'Arts Maisons'];

const ResponsiveAppBar = () => {
  const [anchorElNav, setAnchorElNav] = React.useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleButtonClick = pageURL => {
    var url = window.location.origin + "/comexim/#/";
    window.location.replace(url + pageURL);
    handleCloseNavMenu();
  };

  return (
    <AppBar position="static" sx={{ display: { top: 0 } }}>
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <Typography
            variant="h6"
            noWrap
            component="div"
            onClick={() => handleButtonClick("")}
            sx={{ mr: 2, display: { xs: 'none', md: 'flex' } }}
          >
            Comexim
          </Typography>

          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
              {cataloguePages.map((page) => (
                <MenuItem key={page} onClick={() => handleButtonClick("catalogue/" + page.toLowerCase())}>
                  <Typography textAlign="center">{page}</Typography>
                </MenuItem>
              ))}
              <Divider />
              {pages.map((page) => (
                <MenuItem key={page} onClick={() => handleButtonClick(page.toLowerCase())}>
                  <Typography textAlign="center">{page}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>

          <Typography
            variant="h6"
            noWrap
            component="div"
            onClick={() => handleButtonClick("")}
            sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}
          >
            Comexim
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
              <Button
                key="Catalogue"
                aria-controls="menu-catalogue"
                onClick={handleOpenNavMenu}
                sx={{ my: 2, color: 'white', display: 'block' }}
              >
                Catalogue
              </Button>
              <Menu
              id="menu-catalogue"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'none', md: 'block' },
              }}
            >
              {cataloguePages.map((page) => (
                <MenuItem key={page} onClick={() => handleButtonClick("catalogue/" + page.toLowerCase())}>
                  <Typography textAlign="center">{page}</Typography>
                </MenuItem>
              ))}
            </Menu>


            {pages.map((page) => (
              <Button
                key={page}
                onClick={() => handleButtonClick(page.toLowerCase())}
                sx={{ my: 2, color: 'white', display: 'block' }}
              >
                {page}
              </Button>
            ))}
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};
export default ResponsiveAppBar;